
.journal__main-content {
    display: flex;
}

.journal__sidebar {
    background-color: $dark-grey;
    color: white;
    display: flex;
    flex-direction: column;
    height: 100vh;
    padding-left: 10px;
    padding-right: 10px;
    width: 450px;
}

.journal__sidebar-navbar {
    display: flex;
    justify-content: space-between;

}

.journal__sidebar-navbar h3{
    font-weight: lighter;
}

.journal__new-entry {
    align-items: center;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 30px;
    width: 100%;

    transition: color .3s ease;

    &:hover {
        color: darken($color: white, $amount: 20);
    }
}

.journal__entries {
    flex: 1 1 auto;
    margin-top: 30px;
    overflow-y: scroll;
}

.journal__entries::-webkit-scrollbar {
    display: none;
}

.journal__entry {
    background-color: white;
    border-radius: 4px;
    color: $dark-grey;
    display: flex;
    margin-bottom: 10px;
    overflow: hidden;
    justify-content: space-between;
}

.journal__entry-picture {
    height: 75px;
    width: 75px;
}

.journal__entry-body {
    padding: 5px;
    flex: auto;
}

.journal__entry-title {
    font-size: 17px;
    font-weight: bold;
}

.journal__entry-content {
    font-size: 14px;
    flex-direction: column;
    justify-content: left;
}

.journal__entry-date-box {
    align-items: center;
    display: flex;
    flex-direction: column;
    align-items: end;
    padding: 5px;

    span{
        font-size: 10px;
    }
}